/* Root Font */
// This is based off the P1 styles
$s-font--root-size: 16px;
$s-font--root-weight: 500;
$s-font--root-family: 'GT Walsheim',
sans-serif;
$s-font--root-line: 1.5;


/* Base Font */
$s-font--base-size: 0.875rem;
$s-font--base-size-desktop: 1rem;


/* Heading Font */
$s-font--heading-family: 'Recoleta Medium',
serif;
$s-font--heading-line: 1;
$s-font--heading-weight: 400;

$s-font--heading-1-size: 2rem;
$s-font--heading-1-line: 1.2;
$s-font--heading-1-weight: 500;
$s-font--heading-1-size-laptop: 3rem;
$s-font--heading-1-size-desktop: 4rem;

$s-font--heading-2-size: 1.5rem;
$s-font--heading-2-line: 1.2;
$s-font--heading-2-weight: 500;
$s-font--heading-2-size-laptop: 2.25rem;
$s-font--heading-2-size-desktop: 3rem;

$s-font--heading-3-size: 1.5rem;
$s-font--heading-3-line: 1.3;
$s-font--heading-3-size-laptop: 1.875rem;
$s-font--heading-3-size-desktop: 2.25rem;

$s-font--heading-4-size: 1.25rem;
$s-font--heading-4-line: 1.5;
$s-font--heading-4-size-laptop: 1.5625rem;
$s-font--heading-4-size-desktop: 1.875rem;

$s-font--heading-5-family: $s-font--root-family;
$s-font--heading-5-size: 1.125rem;
$s-font--heading-5-line: 1.5;
$s-font--heading-5-size-laptop: 1.5rem;
$s-font--heading-5-size-desktop: 1.875rem;

$s-font--heading-6-family: $s-font--root-family;
$s-font--heading-6-size: 1rem;
$s-font--heading-6-line: 1.5;
$s-font--heading-6-size-laptop: 1.25rem;
$s-font--heading-6-size-desktop: 1.5rem;

/* Paragraph Font */
$s-font--paragraph-1-size: 1rem;
$s-font--paragraph-1-line: 1.5;
$s-font--paragraph-1-weight: 500;
$s-font--paragraph-1-size-laptop: 1.0625rem;
$s-font--paragraph-1-size-desktop: 1.125rem;

$s-font--paragraph-2-size: 0.9375rem;
$s-font--paragraph-2-line: 1.6;
$s-font--paragraph-2-weight: 400;
// $s-font--paragraph-2-size-laptop: 1.0625rem;
// $s-font--paragraph-2-size-desktop: 1.125rem;

/* Button Font */
$s-font--btn-size: $s-font--paragraph-1-size;
$s-font--btn-line: 1.225;
$s-font--btn-size-laptop: $s-font--paragraph-1-size-laptop;
$s-font--btn-size-desktop: $s-font--paragraph-1-size-desktop;

$s-font--btn-secondary-size: 0.875rem;
$s-font--btn-secondary-line: 1.3;
$s-font--btn-secondary-size-laptop: 1.1875rem;
$s-font--btn-secondary-size-desktop: 1.5rem;

/* Link Font - In the design this is under button styles */
$s-font--link-size: $s-font--paragraph-1-size;
$s-font--link-line: $s-font--paragraph-1-line;
$s-font--link-weight: $s-font--paragraph-1-weight;
$s-font--link-size-laptop: $s-font--paragraph-1-size-laptop;
$s-font--link-size-desktop: $s-font--paragraph-1-size-desktop;


