/*
 *  Body Font
 *    Font applied to the root element
 */
 @mixin t-font--root {
  font-size: $s-font--root-size;
  font-weight: $s-font--root-weight;
  font-family: $s-font--root-family;
  line-height: $s-font--root-line;
}


/* Base Font */
@mixin t-font--base {
  font-size: $s-font--base-size;
}

@mixin t-font--base-desktop {
  font-size: $s-font--base-size-desktop;
}


/* Heading Font */
@mixin t-font--heading {
  margin-top: 0;
  font-family: $s-font--heading-family;
  line-height: $s-font--heading-line;
  font-weight: $s-font--heading-weight;
}

