.c-article-content {
  background-color: $s-color--background-subdued;
  padding: 4rem $s-gutter--edge-mobile 4rem;

  &__content {
    h1, h2, h3, h4, h5, h6 {
      @include t-font--heading-4;
      margin-bottom: 1.625rem;

      &:last-child {
        margin-bottom: 0;
      }
    }

    p {
      @include t-font--paragraph-2;
      margin-bottom: 4rem;

      &:last-child {
        margin-bottom: 0;
      }
    }

    > p, > h1, > h2, > h3, > h4, > h5, > h6 {
      max-width: 46.875rem;
      margin-left: auto;
      margin-right: auto;
    }

    iframe{
      width: 100%;
      max-width: 46.875rem;
    }

    blockquote {
      @include t-font--heading-3;
      font-family: $s-font--heading-family;
      position: relative;
      margin-block-start: 0;
      margin-block-end: 0;
      margin-inline-start: 0;
      margin-inline-end: 0;
      max-width: 58.9375rem;
      margin-left: auto;
      margin-right: auto;
      padding-bottom: 3.8125rem;
      margin-bottom: 4.125rem;
      text-align: center;

      &::after {
        @include t-absolute-center-x();
        display: block;
        content: "";
        width: 100%;
        height: 0.0625rem;
        background-color: $s-color--font-subdued-20;
        bottom: 0;
      }
    }
  }

  &__excerpt {
    position: relative;
    max-width: 46.875rem;
    padding-bottom: 3.8125rem;
    margin: 0 auto 4rem;
    text-align: center;

    &::after {
      @include t-absolute-center-x();
      display: block;
      content: '';
      width: calc(100% + 1.4375rem);
      height: 0.0625rem;
      background-color: $s-color--font-subdued-20;
      bottom: 0;
    }
  }

  &__image-block {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    margin-bottom: 6.3125rem;
    width: calc(100% + #{$s-gutter--edge-mobile} * 2);
    margin-left: -#{$s-gutter--edge-mobile};
  }

  &__image-container {
    padding-bottom: 134.08%;

    &.is-left {
      transform: translateY(2.375rem);
    }
  }

  // Video or Poster
  &__media {
    &-wrapper {
      margin-top: 4rem;
    }

    .o-html5-video__video-container {
      padding-bottom: 86.9047619047619%;
    }
  }

  /*** Media Queries ***/

  @include t-media-query($s-medium-up) {
    padding: 8.125rem #{'min(10%, 23.75rem)'} 6rem;

    &__content {
      blockquote {
        @include t-font--heading-3-laptop;
        line-height: 2.925rem;
        margin-top: 9.625rem;
        margin-bottom: 4.625rem;
        padding-bottom: 7.9375rem;
      }

      h1, h2, h3, h4, h5, h6 {
        @include t-font--heading-4-laptop;
        margin-bottom: 1.2rem;
      }

      p {
        margin-bottom: 3.675rem;
      }
    }

    &__excerpt {
      max-width: 58.875rem;
      padding-bottom: 4.65rem;
      margin-bottom: 2.7375rem;

      &::after {
        width: 100%;
      }
    }

    &__image-block {
      grid-gap: 2.4188rem;
      max-width: 77.625rem;
      width: 100%;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 3.6rem + 4.6875rem; // Add the translation distance
    }

    &__image-container {
      padding-bottom: 121.97%;

      &.is-left {
        transform: translateY(4.6875rem);
      }
    }

    &__media {
      &-wrapper {
        margin: 4.65rem auto 0;
        max-width: 77.625rem;
      }

      .o-html5-video__video-container {
        padding-bottom: #{'min(53.46774193548387%, 41.4375rem)'};
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    padding: 13.5rem #{'min(10%, 23.75rem)'} 10rem;

    &__excerpt {
      padding-bottom: 7.75rem;
      margin-bottom: 4.5625rem;
    }

    &__content {
      blockquote {
        @include t-font--heading-3-desktop;
      }

      h1, h2, h3, h4, h5, h6 {
        @include t-font--heading-4-desktop;
        margin-bottom: 2rem;
      }

      p {
        margin-bottom: 6.125rem;
      }
    }

    &__image-block {
      grid-template-columns: #{'min(49.9194847020934%, 38.75rem)'} #{'min(47.02093397745572%, 36.5rem)'};
      margin-bottom: 6rem + 4.6875rem; // Add the translation distance
    }

    &__image-container {
      &.is-left {
        padding-bottom: 129.0322580645161%;
      }

      &.is-right {
        align-self: self-start;
        padding-bottom: 121.9178082191781%;
      }
    }

    &__media {
      &-wrapper {
        margin-top: 7.75rem;
      }
    }
  }
}